<template>
  <!-- ""   -->
  <div v-if= "changeText">
    <span v-for="item in highlightList" :key="item"
    :class="item.deep ? 'searchMark': ''">{{ item.val }}</span>
  </div>
</template>

<script>
export default {
  data () {
    return {
      highlightList: [] // processed data
    }
  },
  props: {
    innertext: {
      type: String
    },
    innersearch: {
      type: String
    }
  },
  mounted () {
    // console.log(this.innertext)  
    // console.log(this.innertext.indexOf('')) item 
    // console.log(this.innertext.indexOf(''))  => 3; -1
    this.filterHighlight(this.innertext, this.innersearch)
  },
  methods: {
    filterHighlight (text, search) {
      // : list
      const textList = text.split('')
      // ['', '']
      const list = []
      // 
      for (let i = 0; i < textList.length; i++) {
        const obj = {
          deep: false,
          val: textList[i]
        }
        list.push(obj)
      }
      //   console.log(list)
      //     0: {deep: false, val: ''}
      //     1: {deep: false, val: ''}
      //     2: {deep: false, val: ''}
      //     3: {deep: false, val: ''}
      //     0: {deep: false, val: ''}
      //     1: {deep: false, val: ''}
      //     2: {deep: false, val: ''}
      //     3: {deep: false, val: ''}
      // : search
      const searchList = search.split('')
      for (let k = 0; k < searchList.length; k++) {
        // console.log(searchList) ['']
        list.forEach((item) => {
          if (item.val === searchList[k]) {
            // 3 {deep: true, val: ''}
            item.deep = true
          }
        })
      }
      this.highlightList = list
      // console.log(this.highlightList)
      // 0: {deep: false, val: ''}
      // 1: {deep: false, val: ''}
      // 2: {deep: false, val: ''}
      // 3: {deep: true, val: ''}
    }
  },
  computed: {
    changeText () {
      // 
      //    => 
      if (this.innersearch.length > 0 && this.innertext.indexOf(this.innersearch) > -1) {
        this.filterHighlight(this.innertext, this.innersearch)
      }
      return this.innertext
    }
  }
}
</script>
<style>
.searchMark {
  color: var(--third-color);
  font-weight: 600;
}
</style>
